.body {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.dateButton {
  width: 129px;
  height: 32px;
  color: #efede8;
  font-weight: 700;
  line-height: 1.11;
  font-size: 18px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.calendarIconBody {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 2px solid #ef8964;
  border-radius: 4px;
  top: 2px;
  position: relative;
  margin-left: 8px;
}

.calendarIconLine {
  background: #ef8964;
  width: 16px;
  height: 2px;
  position: absolute;
  top: 5px;
  text-align: center;
}

.calendarIconLine span {
  display: inline-block;
  width: 2px;
  height: 5px;
  background: #ef8964;
  transform: translate(-3px, -19px);
  box-sizing: border-box;
  margin-left: 4px;
}

.arrowsBody {
  display: flex;
  margin-left: 20px;
  margin-right: 0px;
  align-items: center;
}

.arrowsBody span {
  margin-left: 10px;
  display: block;
  width: 10px;
  height: 10px;
  border-top: 3px solid #efede8;
  border-left: 3px solid #efede8;
  cursor: pointer;
}

.arrowsLeft {
  transform: rotate(315deg);
}

.arrowsRight {
  transform: rotate(135deg);
}

@media screen and (min-width: 768px) {
  .dateButton {
    line-height: 1.34;
    font-size: 24px;
  }
  .calendarIconBody {
    width: 24px;
    height: 24px;
  }
  .arrowsBody {
    margin-left: 40px;
  }
}
